<template>
  <!-- 投递记录详情页 -->
  <div class="searchResumeDetial_wrapper">
    <!-- 顶部 -->
    <div class="top">
      <img
        class="return_icon"
        src="../../assets/return_icon.png"
        alt="返回图标"
        @click="returnBtnClkFn"
      >
      <span>简历详情</span>
    </div>
    <!-- 用户信息名片 -->
    <div class="infoCard flex_layout">
      <div class="userPic_wrapper">
        <!-- <img class="userPic" src="../../assets/user.png" alt="用户头像">
        <img class="gender_icon manSymbol_icon" src="../../assets/man_symbol.png" alt="男性图标">-->
        <div class="headImg">
          <img
            class="userPic"
            v-if="(drOptions.genderString== '男' || drOptions.genderString == '暂无') && (Number(drOptions.bornInteger)<= 24)"
            :src="resumePicOne"
          >
          <img
            class="userPic"
            v-else-if="(drOptions.genderString == '男' || drOptions.genderString == '暂无') && ((Number(drOptions.bornInteger) >= 25 && Number(drOptions.bornInteger) <= 34) || drOptions.bornInteger == '暂无')"
            :src="resumePicTwo"
          >
          <img
            class="userPic"
            v-else-if="(drOptions.genderString == '暂无') && (drOptions.bornInteger == '暂无')"
            :src="resumePicTwo"
          >
          <img
            class="userPic"
            v-else-if="(drOptions.genderString == '男' || drOptions.genderString == '暂无') && (Number(drOptions.bornInteger) >= 35 && Number(drOptions.bornInteger) <= 44)"
            :src="resumePicThree"
          >
          <img
            class="userPic"
            v-else-if="(drOptions.genderString == '男' || drOptions.genderString == '暂无') && Number(drOptions.bornInteger) >= 45"
            :src="resumePicFour"
          >
          <img
            class="userPic"
            v-else-if="drOptions.genderString == '女' && ( Number(drOptions.bornInteger)<= 24)"
            :src="resumePicFive"
          >
          <img
            class="userPic"
            v-else-if="drOptions.genderString == '女' && ((Number(drOptions.bornInteger) >= 25 && Number(drOptions.bornInteger) <= 34 )|| drOptions.bornInteger == '暂无')"
            :src="resumePicSix"
          >
          <img
            class="userPic"
            v-else-if="drOptions.genderString == '女' && (Number(drOptions.bornInteger) >= 35 && Number(drOptions.bornInteger) <= 44)"
            :src="resumePicServen"
          >
          <img
            class="userPic"
            v-else-if="drOptions.genderString == '女' && Number(drOptions.bornInteger) >= 45"
            :src="resumePicEight"
          >

          <img
            v-if="drOptions.genderString == '男'"
            class="gender_icon manSymbol_icon"
            src="../../assets/man_symbol.png"
            alt="男性图标"
          >
          <img
            v-else
            class="gender_icon manSymbol_icon"
            src="../../assets/woman_symbol.png"
            alt="女性图标"
          >
        </div>
      </div>
      <div class="infos">
        <p class="flex_layout lh-1">
          <span class="name">{{drOptions.resumeName}}</span>
          <span class="gender_label flex_layout">{{drOptions.genderString}}</span>
        </p>
        <p class="text-left lh-1">
          <span class="c-818D8D f-24">更新时间:</span>
          <span class="c-354242 f-24" v-if="updateTimeRst != ''">{{updateTimeRst}}</span>
          <span v-else class="c-354242 f-24">暂无</span>
        </p>
      </div>
    </div>
    <!-- 基本信息 -->
    <div class="basicInfo_box">
      <p class="basicInfo_title title f-28">基本信息</p>
      <ul class="infos_list">
        <li>
          <label class="tel_label flex_layout">
            <span>电</span>
            <span>话</span>
          </label>
          <span>
            <a class="blue fw-550" :href="'tel:' + drOptions.mobile" ref="tel">{{drOptions.mobile}}</a>
          </span>
        </li>
        <li>
          <label>籍贯城市</label>
          <span v-if="nationalPlace != ''">{{nationalPlace}}</span>
          <span v-else>暂无</span>
        </li>
        <li>
          <label>期望薪资</label>
          <span v-if="drOptions.salaryFrom != '暂无'">{{drOptions.salaryFrom}}-{{drOptions.salaryTo}}</span>
          <span v-else>暂无</span>
        </li>
        <li>
          <label>最高学历</label>
          <span v-if="drOptions.education != ''">{{drOptions.education}}</span>
          <span v-else>暂无</span>
        </li>       
        <li>
          <label>工作年限</label>
          <span>{{drOptions.workYearInteger}}</span>
        </li>
        <li>
          <label>意向岗位</label>
          <div class="intentionWorkType_wrapper">
            <span v-if="drOptionsDemand.jobTypeString != '' ? drOptionsDemand.jobTypeString : '暂无'">{{drOptionsDemand.jobTypeString}}</span>
          </div>         
        </li>
         <li>
          <label>意向城市</label>
          <span class="intentionCity" v-if="drOptions.intentionalCity != ''">
            <span
              class="intentionCity"
              v-for="(item,index) in drOptions.intentionalCity"
              :key="index"
            >{{item.intentionalPlace}}</span>
          </span>
          <span v-else>暂无</span>
        </li>
        <!-- <li>
          <label>投递来源</label>
          <span v-if="drOptions.source != '' ? drOptions.source : '暂无'">{{drOptions.source}}</span>
        </li> -->
        
      </ul>
    </div>
    <!-- 投递职位信息 -->
    <div class="deliverPositionInfo_box">
      <p class="basicInfo_title title f-28">投递职位信息</p>
      <ul class="dpi_list">
        <li>
          <label>投递企业</label>
          <p class="height_auto">{{drOptionsDemand.corpName}}</p>
        </li>
        <li>
          <label>求职岗位</label>
          <p class="height_auto">{{drOptionsDemand.jobTypeString}}</p>
        </li>
        <li>
          <label>职位标题</label>
          <p class="height_auto">{{drOptionsDemand.title}}</p>
        </li>
        <li>
          <label>招聘人数</label>
          <p class="height_auto">{{drOptionsDemand.recruitNum}}</p>
        </li>
        <li>
          <label>招聘类型</label>
          <p class="height_auto">{{drOptionsDemand.recruitDemandType}}</p>
        </li>
        <li>
          <label>结算标准</label>
          <p class="height_auto">{{drOptionsDemand.settlementStandard}}</p>
        </li>
        <li>
          <label>发薪时间</label>
          <p class="height_auto">{{drOptionsDemand.payArrangement}}</p>
        </li>
        <li>
          <label>福利标签</label>
          <p class="height_auto">{{welfareLabelRsl}}</p>
        </li>
        <li>
          <label>年龄要求</label>
          <p class="height_auto">{{drOptionsDemand.ageFrom}}</p>
        </li>
        <li>
          <label>学历要求</label>
          <p class="height_auto">{{drOptionsDemand.educationRequired}}</p>
        </li>
        <li>
          <label>性别要求</label>
          <p class="height_auto">{{drOptionsDemand.genderRequired}}</p>
        </li>
        <li>
          <label>工作时间</label>
          <p class="height_auto">{{drOptionsDemand.workTime}}</p>
        </li>
        <li>
          <label>工作地址</label>
          <p class="height_auto">{{drOptionsDemand.workAddress}}</p>
        </li>
        <li>
          <label>工作内容</label>
          <p class="work_cnt">{{drOptionsDemand.jobContent}}</p>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
// 我的简历用户头像
import resumePicOne from "@/assets/post/boy_1.png";
import resumePicTwo from "@/assets/post/boy_2.png";
import resumePicThree from "@/assets/post/boy_3.png";
import resumePicFour from "@/assets/post/boy_4.png";
import resumePicFive from "@/assets/post/girl_1.png";
import resumePicSix from "@/assets/post/girl_2.png";
import resumePicServen from "@/assets/post/girl_3.png";
import resumePicEight from "@/assets/post/girl_4.png";

export default {
  data() {
    return {
      deliverRecordListData: "", // 投递记录列表数据
      currentResumeId: "", // 用户resumeId
      drOptions: [], // 当前用户数据
      drOptionsDemand: [], // 用户数据列表demand
      // 我的简历用户头像
      resumePicOne: resumePicOne,
      resumePicTwo: resumePicTwo,
      resumePicThree: resumePicThree,
      resumePicFour: resumePicFour,
      resumePicFive: resumePicFive,
      resumePicSix: resumePicSix,
      resumePicServen: resumePicServen,
      resumePicEight: resumePicEight,
      updateTimeIdArrData: [], // storage获取更新时间及id
      updateTimeRst: "", // storage获取简历更新时间
      nationalPlace: "", // storage获取籍贯
      welfareLabelRsl: "" // 福利标签添加斜杠
    };
  },
  methods: {
    // 左上角按钮点击事件
    returnBtnClkFn() {
      this.$router.push({ path: "/qdsSearchResume", query: { id: 2 } }); // 返回投递记录列表页
    },
    init() {
      var that = this;
      var id = that.currentResumeId;
      that.deliverRecordListData.forEach((item, index) => {
        // index 从 0 开始计数
        if (item.resume.resumeId == id) {
          that.drOptions = item.resume;
          that.drOptionsDemand = item.demand;
        }
      });

      // 福利标签字符串斜杠分割拼接
      if (that.drOptionsDemand.welfareLabel.length != 0) {
        that.drOptionsDemand.welfareLabel.forEach((item, index) => {
          that.welfareLabelRsl += item + "/";
        });

        if (that.welfareLabelRsl.length > 0) {
          let welfLabelLen = that.welfareLabelRsl.length;
          that.welfareLabelRsl = that.welfareLabelRsl.substring(
            0,
            welfLabelLen - 1
          );
        }
        return that.welfareLabelRsl;
      } else {
        that.welfareLabelRsl = "暂无";
      }
    }
  },
  mounted() {
    var that = this;
    var deliverRecordList = localStorage.getItem("deliverRecordList");
    that.deliverRecordListData = JSON.parse(deliverRecordList); // 把字符串转换成JSON对象
    that.currentResumeId = localStorage.getItem("deliverRecordId"); // 当前点击的简历id
    this.init(); // 首次页面数据的处理  或 刷新后的页面数据处理    
    // 获取myResumeDetial页面存储的简历更新时间以及对应id
    var updateTimeIdArr = localStorage.getItem("updateTimeArrStorage");
    that.updateTimeIdArrData = JSON.parse(updateTimeIdArr);
    that.updateTimeIdArrData.forEach((item, index) => {
      if (item.id == that.currentResumeId) {
        //  currentResumeId 为当前页面简历id
        that.updateTimeRst = item.updateTime; // 更新时间
        that.nationalPlace = item.nationalPlace; // 籍贯
      }
    });
  }
};
</script>

<style scoped>
.searchResumeDetial_wrapper {
  width: 100%;
  height: 100%;
  background: #f5f5f5;
}

.searchResumeDetial_wrapper .top {
  width: 100%;
  height: 2.75rem;
  line-height: 2.75rem;
  background: #fff;
  position: relative;
  text-align: center;
}

.searchResumeDetial_wrapper .top .return_icon {
  width: 0.59375rem;
  height: 1.21875rem;
  position: absolute;
  top: 50%;
  left: 0.9375rem;
  margin-top: -0.609375rem;
}

.searchResumeDetial_wrapper .top > span {
  color: #0b231f;
  font-size: 1rem;
}

.searchResumeDetial_wrapper .infoCard {
  width: 92%;
  height: 5.90625rem;
  background: #fff;
  margin: 0.9375rem auto 0.9375rem;
  padding: 0.9375rem 0 1.28125rem 0.9375rem;
  box-sizing: border-box;
  justify-content: space-between;
  box-shadow: 0px 1px 2px #888888;
}

.searchResumeDetial_wrapper .infoCard .userPic_wrapper {
  position: relative;
  width: 3.6875rem;
  height: 3.6875rem;
}

.searchResumeDetial_wrapper .infoCard .userPic {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 1px solid #e5e5e5;
}

.searchResumeDetial_wrapper .infoCard .userPic_wrapper .gender_icon {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 1rem;
  height: 1rem;
}

.searchResumeDetial_wrapper .infoCard .infos {
  width: 16.9375rem;
  height: 3.0375rem;
  padding-left: 1.46875rem;
  box-sizing: border-box;
}

.searchResumeDetial_wrapper .infoCard .infos p:first-child {
  justify-content: flex-start;
  margin-bottom: 0.625rem;
}

.searchResumeDetial_wrapper .infoCard .infos .name {
  color: #0b231f;
  font-size: 1.125rem;
  margin-right: 0.9375rem;
}

.searchResumeDetial_wrapper .infoCard .infos .gender_label {
  width: 2.1875rem;
  height: 1.0625rem;
  line-height: 1.0625rem;
  background: #e9effb;
  color: #4f86f8;
  font-size: 0.75rem;
}

.searchResumeDetial_wrapper .basicInfo_box {
  width: 100%;
  /* height: 15.6875rem; */
  background: #fff;
  padding: 0 0.9375rem;
  box-sizing: border-box;
  margin-bottom: 0.9375rem;
}

.searchResumeDetial_wrapper .title {
  width: 100%;
  height: 2.6875rem;
  line-height: 2.6875rem;
  border-bottom: 1px solid #e5e5e5;
  text-align: left;
  color: #0b231f;
}

.searchResumeDetial_wrapper .basicInfo_box .infos_list {
  width: 100%;  
  padding-bottom: 1.25rem;
  box-sizing: border-box;
}

.searchResumeDetial_wrapper .basicInfo_box .infos_list li {
  width: 100%;
  margin-top: 0.65rem;
  display: flex;
  display: -webkit-flex;
  justify-content: flex-start;
  align-items: center;
}

.searchResumeDetial_wrapper .basicInfo_box .infos_list li label {
  color: #818d8d;
  font-size: 0.8125rem;
  margin-right: 1.28125rem;
  width: 3.525rem;
  flex-shrink: 0;
}

.searchResumeDetial_wrapper .basicInfo_box .infos_list li > span {
  color: #0b231f;
  font-size: 0.8125rem;
  /* width: 5.75rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; */
  text-align: left;
}

.searchResumeDetial_wrapper .basicInfo_box .infos_list li .tel_label {
  justify-content: space-between;
}

.searchResumeDetial_wrapper .deliverPositionInfo_box {
  width: 100%;
  background: #fff;
  padding: 0 0.9375rem;
  box-sizing: border-box;
}

.searchResumeDetial_wrapper .deliverPositionInfo_box .dpi_list li {
  display: flex;
  display: -webkit-flex;
  justify-content: flex-start;
}

.searchResumeDetial_wrapper .deliverPositionInfo_box .dpi_list li > label {
  width: 4.6875rem;
  height: 2.6875rem;
  line-height: 2.6875rem;
  color: #7e8d8d;
  font-size: 0.8125rem;
  text-align: left;
}

.searchResumeDetial_wrapper .deliverPositionInfo_box .dpi_list li > p {
  width: 16.9375rem;
  height: 2.59375rem;
  border-bottom: 1px solid #e5e5e5;
  color: #00241f;
  font-size: 0.8125rem;
  display: flex;
  display: -webkit-flex;
  justify-content: flex-start;
  align-items: center;
}

.searchResumeDetial_wrapper .deliverPositionInfo_box .dpi_list li .work_cnt {
  border-bottom: 0;
  height: auto;
  padding: 0.875rem 0 1.375rem 0;
  box-sizing: border-box;
}

.searchResumeDetial_wrapper .deliverPositionInfo_box .dpi_list li .height_auto{
  height: auto;
  padding: 0.75rem 0;
  box-sizing: border-box;
}

@media screen and (min-width: 320px) {
  .searchResumeDetial_wrapper .basicInfo_box .infos_list li .tel_label {
    padding: 0 0.1875rem;
    box-sizing: border-box;
  }
}

@media screen and (max-width: 320px) {
  .searchResumeDetial_wrapper .basicInfo_box .infos_list li .tel_label {
    padding: 0;
    box-sizing: border-box;
  }
}

.intentionWorkType_wrapper{
    color: #0b231f;
    font-size: 0.8125rem;
    text-align: left;
}

/* --公用样式-- */
.text-left {
  text-align: left;
}

.c-818D8D {
  color: #818d8d;
}

.c-354242 {
  color: #354242;
}

.blue {
  color: #2da1a4;
  text-decoration: underline;
}

.f-24 {
  font-size: 0.75rem;
}

.f-28 {
  font-size: 0.875rem;
}

.flex_layout {
  display: flex;
  display: -webkit-flex;
  justify-content: center;
  align-items: center;
}

.fw-550 {
  font-weight: 550;
}

.lh-1 {
  line-height: 1;
}
</style>



